// Container widths
$container-full: 2680px;
$container-width: 1220px;
$container-narrow-width: 990px;

// Colors
$primary-color:		#000000;
$secondary-color:	blue;
$tertiary-color:	red;
$white:				#ffffff;
$black:				#000000;
$gray:				#6e6f71;
$gray-dark:         #333333;
$teal:              #09b3c8;
$teal-light:        #dfeeff;
$yellow:            #fccf33;

// Fonts
// $primary-font: 'Lato', sans-serif;
$primary-font: 'Open Sans', sans-serif;;
$secondary-font: sans-serif;

// Font Sizes
$font-size-xsmall:	12px;
$font-size-small:	14px;
$font-size-base:	16px;
$font-size-large:	18px;
$font-size-xlarge:	20px;
$font-size-22:      22px;

// Heading sizes
$font-size-h1: 50px;
$font-size-h2: 36px;
$font-size-h3: 24px;
$font-size-h4: 20px;
$font-size-h5: 18px;
$font-size-h6: 15px;

// Breakpoints
$mac: 1440px;
$lt: 1366px;
$ls: 1280px;
$lg: 1199px;
$md: 991px;
$sm: 767px;
$mb: 480px;
$ip: 420px;
$xs: 350px;

// Navigation config
$responsive-toggle-breakpoint: $lg;
$toggle-menu-height: 20px;
$toggle-menu-width: 25px;
$toggle-menu-bar-height: 3px;
$toggle-menu-bar-color: $primary-color;
$toggle-menu-bar-active-color: transparent;

// Transition
$dur: 350ms;
$ease: ease;